
#resumePreview {
  color: yellowgreen;
  width: 65%;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  background: #444;
  padding: 2em;
  line-height: 1.2;
  overflow: auto;
}
#resumePreview section + section {
    margin-top: 2em;
}
#resumePreview p {
    white-space: pre-line;
}
#resumePreview section > h2:first-child {
    background: #ddd;
    display: inline-block;
    padding: .2em;
    margin-bottom: .5em;
}
#resumePreview section[data-name="profile"] > h1 {
    margin: .1em 0;
    font-size: 4em;
}
#resumePreview section[data-name="work history"] li + li,
  #resumePreview section[data-name="projects"] li + li,
  #resumePreview section[data-name="awards"] li + li {
    margin-top: 1em;
}
#resumePreview section[data-name="work history"] li h3,
  #resumePreview section[data-name="projects"] li h3,
  #resumePreview section[data-name="awards"] li h3 {
    border-bottom: 1px solid #999;
    padding-bottom: .3em;
    margin-bottom: .3em;
}
#resumePreview section[data-name="education"] li {
    line-height: 1.5;
}



