
ol {
  list-style: none;
}
#resumeEditor {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  background: white;
  margin-right: 16px;
  width: 35%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  overflow: auto;
}
#resumeEditor nav {
    background: #000;
    /*width:80px;*/
    min-height: 610px;
}
#resumeEditor nav > ol > li {
      width: 80px;
      height: 48px;
      color: white;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      margin: 16px 0;
      cursor: pointer;
}
#resumeEditor nav > ol > li.active {
        background: white;
        color: black;
}
#resumeEditor nav > ol > li svg.icon {
        width: 24px;
        height: 24px;
        fill: currentcolor;
}
#resumeEditor .panels {
    /*background:orange;*/
    /*min-height:610px;*/
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    /*display:flex;*/
    /*justify-content: center;*/
    /*min-width:386px;*/
}
#resumeEditor .panels li {
      /*.subitem:nth-child(1){*/
      /*margin-top:24px;*/
      /*}*/
      margin: 8px 24px;
}
#resumeEditor .panels li .labInp {
        /*display:flex;*/
        /*flex-direction:column;*/
        /*justify-content: space-between;*/
        /*margin-left:24px;*/
        /*margin-right:24px;*/
        /*margin-bottom:16px;*/
}
#resumeEditor .panels li .labInp label {
          display: block;
          font-size: 20px;
          color: #333333;
          letter-spacing: 0;
          padding: 16px 0;
}
#resumeEditor .panels li .labInp input {
          background: #ffffff;
          border: 1px solid #dddddd;
          box-shadow: inset 0 1px 3px 0 rgba(0, 0, 0, 0.25);
          width: 100%;
          height: 38px;
          font-size: 20px;
}
#resumeEditor .panels li:nth-child(n+2) .subitem {
      border-bottom: 1px solid #ccc;
      padding-bottom: 10px;
}



